import React from "react";
import { connect } from 'react-redux';
import {shuffleArray, doubleValues} from '../../../Utils';
import Card from "../Card";
import GameControl from '../GameControl';
import GameInfo from '../GameInfo';
import UserInfo from '../UserInfo';
import Modal from '../Modal';
import "./index.css";

const SERVER_URL = 'http://mmg-score.herokuapp.com/';

const mapStateToProps = state => {
  return {username: state.username, email: state.email};
}

class ConnectedGameField extends React.Component {

  state = {
    flipState: [...Array(16).fill(false)],
    stepsCount: 0,
    time: 0,
    winModalStatus: '',
  }

  constructor(props) {
    super(props);

    this.VALUES = [
      '🍒',
      '🐱',
      '🎸',
      '🏰',
      '🎮',
      '☀️',
      '🎧',
      '🍩',
    ];

    let currArray = this.VALUES.slice(0, this.props.size / 2);

    this.currentCardsValues = shuffleArray(doubleValues(currArray));
    this.correctStepsCount = 0;

    this.selectedCards = [];
  }

  /**
   * Send user score to server
   */
  sendScore() {
    const user = {
      username: this.props.username,
      email: this.props.email,
      score: this.state.stepsCount,
    }

    fetch(SERVER_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
        body: JSON.stringify(user)
    });
  }

  resetGame() {
    clearInterval(this.gameTimer);
    this.correctStepsCount = 0;
    this.selectedCards = [];

    let currArray = this.VALUES.slice(0, this.props.size / 2);
    this.currentCardsValues = shuffleArray(doubleValues(currArray));

    this.setState({
      flipState: [...Array(16).fill(false)],
      stepsCount: 0,
      time: 0,
      winModalStatus: '',
    });
  }

  componentWillUnmount() {
    clearInterval(this.gameTimer);
  }

  isGameOver() {
    return this.correctStepsCount === (this.props.size / 2)
  }

  handleNewGameButton = event => {
    this.resetGame();
  }

  handleWinModalClose = event => {
    this.setState({
      winModalStatus: 'hide',
    })
  }

  handleCardClick = key => event => {
    const flipState = this.state.flipState.slice();
    if(flipState[key] === true) {
      return;
    }

    // start timer on first move
    if (this.state.stepsCount === 0) {
      this.gameTimer = setInterval(() => {
        const sec = this.state.time;
        this.setState({
          time: sec + 1,
        })
      }, 1000);
    }

    // flip selected card
    flipState[key] = !flipState[key];
    const steps = this.state.stepsCount;
    this.setState({
      flipState: flipState,
      stepsCount: steps + 1,
    });

    this.selectedCards.push(key);

    if (this.selectedCards.length === 2) {

      const firstValue = this.currentCardsValues[this.selectedCards[0]];
      const secondValue = this.currentCardsValues[this.selectedCards[1]];

      if (firstValue === secondValue) {
        this.correctStepsCount += 1; // one more correct guess
        this.selectedCards = [];
      } else {
        setTimeout(() => {
          this.selectedCards.forEach(i => {flipState[i] = false});
          this.setState({
            flipState: flipState,
          });
          this.selectedCards = [];
        }, 500);
      }
    }

    // check win condition
    if (this.isGameOver()) {
      clearInterval(this.gameTimer);
      setTimeout(() => {
        this.sendScore();
        this.setState({
          winModalStatus: 'show',
        })
      }, 800)
    }
  }

  render() {
    // fill gamefield with cards
    const cards = [...Array(this.props.size)].map((_, i) => (
      <Card key={i} value={this.currentCardsValues[i]} flip={this.state.flipState[i]} onClick={this.handleCardClick(i)}/>
    ));

    return (
      <section>
        <div className="game-info-container">
          <UserInfo username={this.props.username}/>
          <GameControl newGame={this.handleNewGameButton}/>
          <GameInfo steps={this.state.stepsCount} time={this.state.time} />
          <Modal status={this.state.winModalStatus} header="You won!" body="Your score saved." onClose={this.handleWinModalClose}/>
        </div>
        <div className="game-field">{cards}</div>
        
      </section>
    );
  }
}

const GameField = connect(mapStateToProps) (ConnectedGameField);

export default GameField;



