import React from 'react';
import { connect } from 'react-redux';
import { addUserInfo } from '../../../actions';
import './index.css';

const mapDispatchToProps = dispatch => {
  return {
    addUserInfo: user => dispatch(addUserInfo(user))
  };
}

const mapStateToProps = state => {
  return {username: state.username, email: state.email};
}

class ConnectedRegistrationForm extends React.Component {

  handleSubmit = event => {
    event.preventDefault();
    this.props.addUserInfo({username: this.username.value, email: this.email.value});
    // not the best practice
    this.props.history.push('/game');
  }

  render() {
    return (
      <div className="registration-form">
      <form onSubmit={this.handleSubmit}>
        <div>
        <label>
          Username<br/>
          <input type="text" required placeholder="Enter your username"
            ref={input => this.username = input}/>
        </label>
        </div>

        <div>
        <label>
          Email<br/>
          <input type="email" required placeholder="Enter your email"
            ref={input => this.email = input}/>
        </label>
        </div>
        <div>
          <button type="submit">Register</button>
        </div>
      </form>
      </div>
    );
  }
}

const RegistrationForm = connect(mapStateToProps, mapDispatchToProps) (ConnectedRegistrationForm);

export default RegistrationForm;



