import React from 'react';
import './index.css';

const ScoreTable = (props) => {
  const players = props.results;
  const resultsList = players.map(item => 
    <li key={item._id}>{item.username} ({item.email}): {item.score} points</li>
  );
  return (
    <ol>
      {resultsList}
    </ol>
  );
}
export default ScoreTable;



