import React, { Component } from 'react';
import './App.css';
import Navigation from './components/Navigation';
import Main from './components/Main';

class App extends Component {
  render() {
    return (
      <main className="container">
        <h1>Match-match game react way</h1>
        <Navigation />
        <Main />
      </main>
    );
  }
}

export default App;



