import React from 'react';
import './index.css';

const Card = (props) => {
  const flipContainer = props.flip ? 'flip-container flip' : 'flip-container';

  return (
    <div className={flipContainer} onClick={props.onClick}>
      <div className="flipper">
        <div className="front first-pattern">
        </div>
        <div className="back">
          <span>{props.value}</span>
        </div>
      </div>
    </div>
  );
}

export default Card;



