import React from 'react';
import ScoreTable from './ScoreTable';
import Spinner from './Spinner';

export default class Scoreboard extends React.Component {
  state = {
    usersScore: [],
    loading: false,
  }

  /**
   * Sort users by score
   * @param {Object} a first user
   * @param {Object} b second user
   */
  sortByScore(a, b) {
    return b.score - a.score;
  }

  /**
   * Get score from server
   */
  fetchScore() {
    fetch('http://mmg-score.herokuapp.com/')
      .then(responce => responce.json())
      .then(data => {
        this.setState({
          usersScore: data.result.sort(this.sortByScore),
          loading: false,
        });
      });
  }

  componentDidMount() {
    this.setState({
      loading: true,
    })
    this.fetchScore();
  }
  
  render(){
    return (
      <section>
        <ScoreTable results={this.state.usersScore} />
        <Spinner loading={this.state.loading}/>
      </section>
    )
  }
}


