import React from 'react';
import RegistrationForm from './RegistrationForm';
import './index.css';

const Registration = (props) => (
  <section className="intro">
    <div>
      <RegistrationForm history={props.history}/>
    </div>
    
    <div className="intro-text">
    <p>How to play: Memory is counter game where the objective is to find pairs. When the game begins, all pictures are hidden.</p>
    <p>To Play:</p>
    <ol>
      <li>Select two cards to try to match the pictures</li>
      <li>If you match the pictures you can select next two pictures</li>
      <li>If they don't match cards flip back</li>
    </ol>
    </div>
  </section>
);

export default Registration;



