import React from 'react';
import './index.css';

const Modal = (props) => {
  const status = props.status;
  return (
    <section className={"win-modal " + status}>
      <div><h3>{props.header}</h3></div>
      <div><p>{props.body}</p></div>
      <div><button onClick={props.onClose}>Close</button></div>
    </section>
  );
}

export default Modal;



