import React from 'react';
import { Link } from 'react-router-dom';
import './index.css';


const UserInfo = (props) => (
  <div>
    <p className="user-name">{ props.username }</p>
    <Link to='/'>Change user</Link>
  </div>
);

export default UserInfo;



