import { ADD_USER_INFO } from '../actions/actions';

const initialState = {
  username: 'John Doe',
  email: 'test@email.com',
}

const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_USER_INFO:
      return {username: action.user.username, email: action.user.email };
    default:
      return state;
  }
};

export default rootReducer;



