import React from 'react';
import { Link } from 'react-router-dom';
import './index.css';

const Navigation = () => (
  <nav>
    <ul>
      <li><Link to='/'>Home</Link></li>
      <li><Link to='/game'>Game</Link></li>
      <li><Link to='/scoreboard'>Scoreboard</Link></li>
    </ul>
  </nav>
);

export default Navigation;



